body{
  background-color: rgb(179, 156, 156);
}
.App {
  padding-top: 20px;
  text-align: center;
}

input {
  font-size: 16px;
  padding: 10px 10px 10px 5px;
  width: 200px;
  border: none;
  border-bottom: 1px solid #515151;
  background: transparent;
}

input:focus {
  outline: none;
}

.add_btn {
  margin: 12px;
  height: 50px;
  width: 120px;
  border-radius: 10px;
  background: #333;
  justify-content: center;
  align-items: center;
  box-shadow: -5px -5px 15px #444, 5px 5px 15px #222, inset 5px 5px 10px #444,
    inset -5px -5px 10px #222;
  font-family: "Damion", cursive;
  cursor: pointer;
  border: none;
  font-size: 16px;
  color: rgb(161, 161, 161);
  transition: 500ms;
}

.add_btn:hover {
  box-shadow: -5px -5px 15px #444, 5px 5px 15px #222, inset 5px 5px 10px #222,
    inset -5px -5px 10px #444;
  color: #d6d6d6;
  transition: 500ms;
}


